import { type ClassValue, clsx } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

export function calculateConversion(
  amount: number,
  direction: 'BOLIVIA_CHILE' | 'CHILE_BOLIVIA'
): { result: number; commission: number; exchangeRate: number } {
  // Validar entrada
  if (amount < 0 || isNaN(amount)) {
    throw new Error('La cantidad debe ser un número positivo');
  }

  if (amount === 0) {
    return { result: 0, commission: 0, exchangeRate: 0 };
  }

  // Determinar tasa de comisión basada en el día de la semana:
  // 3% de comisión durante fin de semana y 2% en días laborables
  const today = new Date().getDay();
  const commissionRate = (today === 0 || today === 6) ? 0.03 : 0.02;

  // Tipos de cambio actualizados (valores aproximados)
  let exchangeRate = 1;
  if (direction === 'BOLIVIA_CHILE') {
    // 1 Boliviano (BOB) ≈ 110 Pesos Chilenos (CLP)
    exchangeRate = 110;
  } else if (direction === 'CHILE_BOLIVIA') {
    // 1 Peso Chileno (CLP) ≈ 0.009 Bolivianos (BOB)
    exchangeRate = 0.009;
  }

  const commission = amount * commissionRate;
  const netAmount = amount - commission;
  const result = netAmount * exchangeRate;

  return {
    result: Math.round(result * 100) / 100, // Redondear a 2 decimales
    commission: Math.round(commission * 100) / 100,
    exchangeRate
  };
}

export function formatCurrency(amount: number, currency: 'BOB' | 'CLP'): string {
  const currencySymbols = {
    BOB: 'Bs.',
    CLP: '$'
  };
  
  return `${currencySymbols[currency]} ${amount.toLocaleString('es-ES', { 
    minimumFractionDigits: 2, 
    maximumFractionDigits: 2 
  })}`;
}
