"use client"

import React, { useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { RadioGroup, RadioGroupItem } from '@/components/ui/radio-group';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { calculateConversion, formatCurrency } from '@/lib/utils';

export default function CurrencyConverter() {
  const [amount, setAmount] = useState<string>('');
  const [direction, setDirection] = useState<'BOLIVIA_CHILE' | 'CHILE_BOLIVIA'>('BOLIVIA_CHILE');
  const [result, setResult] = useState<{
    result: number;
    commission: number;
    exchangeRate: number;
  } | null>(null);
  const [error, setError] = useState<string>('');
  const [isCalculating, setIsCalculating] = useState<boolean>(false);

  const handleAmountChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value;
    setAmount(value);
    setError('');
    setResult(null);
  };

  const handleDirectionChange = (value: string) => {
    setDirection(value as 'BOLIVIA_CHILE' | 'CHILE_BOLIVIA');
    setResult(null);
    setError('');
  };

  const handleCalculate = () => {
    try {
      setIsCalculating(true);
      setError('');
      
      const numAmount = parseFloat(amount);
      
      if (isNaN(numAmount) || numAmount <= 0) {
        throw new Error('Por favor ingrese una cantidad válida mayor a 0');
      }

      const conversionResult = calculateConversion(numAmount, direction);
      setResult(conversionResult);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Error en el cálculo');
      setResult(null);
    } finally {
      setIsCalculating(false);
    }
  };

  const handleWhatsApp = () => {
    if (!result) return;

    const fromCurrency = direction === 'BOLIVIA_CHILE' ? 'BOB' : 'CLP';
    const toCurrency = direction === 'BOLIVIA_CHILE' ? 'CLP' : 'BOB';
    const fromCountry = direction === 'BOLIVIA_CHILE' ? 'Bolivia' : 'Chile';
    const toCountry = direction === 'BOLIVIA_CHILE' ? 'Chile' : 'Bolivia';

    const message = `Hola! Me interesa hacer una transferencia de dinero:

💰 Cantidad a enviar: ${formatCurrency(parseFloat(amount), fromCurrency)}
📍 Desde: ${fromCountry}
📍 Hacia: ${toCountry}
💵 Cantidad a recibir: ${formatCurrency(result.result, toCurrency)}
📊 Comisión: ${formatCurrency(result.commission, fromCurrency)}

¿Podrían ayudarme con el proceso?`;

    const encodedMessage = encodeURIComponent(message);
    const whatsappUrl = `https://wa.me/59178123456?text=${encodedMessage}`;
    
    window.open(whatsappUrl, '_blank');
  };

  const getSourceCurrency = () => direction === 'BOLIVIA_CHILE' ? 'BOB' : 'CLP';
  const getTargetCurrency = () => direction === 'BOLIVIA_CHILE' ? 'CLP' : 'BOB';
  const getSourceCountry = () => direction === 'BOLIVIA_CHILE' ? 'Bolivia' : 'Chile';
  const getTargetCountry = () => direction === 'BOLIVIA_CHILE' ? 'Chile' : 'Bolivia';

  return (
    <Card className="w-full max-w-2xl mx-auto">
      <CardHeader className="text-center">
        <CardTitle className="text-2xl font-bold text-gray-900">
          Calculadora de Transferencias
        </CardTitle>
        <CardDescription className="text-gray-600">
          Calcula cuánto dinero recibirás en tu transferencia internacional
        </CardDescription>
      </CardHeader>
      
      <CardContent className="space-y-6">
        {/* Selector de Dirección */}
        <div className="space-y-3">
          <Label className="text-base font-medium">Dirección de la transferencia:</Label>
          <RadioGroup
            value={direction}
            onValueChange={handleDirectionChange}
            className="grid grid-cols-1 gap-4"
          >
            <div className="flex items-center space-x-3 p-4 border rounded-lg hover:bg-gray-50">
              <RadioGroupItem value="BOLIVIA_CHILE" id="bolivia-chile" />
              <Label htmlFor="bolivia-chile" className="flex-1 cursor-pointer">
                <div className="font-medium">Bolivia → Chile</div>
                <div className="text-sm text-gray-500">Enviar Bolivianos (Bs.) - Recibir Pesos Chilenos ($)</div>
              </Label>
            </div>
            <div className="flex items-center space-x-3 p-4 border rounded-lg hover:bg-gray-50">
              <RadioGroupItem value="CHILE_BOLIVIA" id="chile-bolivia" />
              <Label htmlFor="chile-bolivia" className="flex-1 cursor-pointer">
                <div className="font-medium">Chile → Bolivia</div>
                <div className="text-sm text-gray-500">Enviar Pesos Chilenos ($) - Recibir Bolivianos (Bs.)</div>
              </Label>
            </div>
          </RadioGroup>
        </div>

        {/* Input de Cantidad */}
        <div className="space-y-2">
          <Label htmlFor="amount" className="text-base font-medium">
            Cantidad a enviar desde {getSourceCountry()}:
          </Label>
          <div className="relative">
            <Input
              id="amount"
              type="number"
              placeholder="0.00"
              value={amount}
              onChange={handleAmountChange}
              className="text-lg py-3 pl-12"
              min="0"
              step="0.01"
            />
            <div className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-500 font-medium">
              {getSourceCurrency() === 'BOB' ? 'Bs.' : '$'}
            </div>
          </div>
        </div>

        {/* Error */}
        {error && (
          <Alert className="border-red-200 bg-red-50">
            <AlertDescription className="text-red-700">{error}</AlertDescription>
          </Alert>
        )}

        {/* Botón Calcular */}
        <Button 
          onClick={handleCalculate}
          disabled={!amount || isCalculating}
          className="w-full py-3 text-lg font-medium"
          size="lg"
        >
          {isCalculating ? 'Calculando...' : 'Calcular Conversión'}
        </Button>

        {/* Resultado */}
        {result && (
          <div className="space-y-4 p-6 bg-green-50 border border-green-200 rounded-lg">
            <div className="text-center">
              <div className="text-sm text-gray-600 mb-2">Recibirás en {getTargetCountry()}:</div>
              <div className="text-3xl font-bold text-green-700">
                {formatCurrency(result.result, getTargetCurrency())}
              </div>
            </div>
            
            <div className="grid grid-cols-2 gap-4 text-sm">
              <div className="text-center p-3 bg-white rounded border">
                <div className="text-gray-500">Comisión</div>
                <div className="font-medium text-gray-900">
                  {formatCurrency(result.commission, getSourceCurrency())}
                </div>
              </div>
              <div className="text-center p-3 bg-white rounded border">
                <div className="text-gray-500">Tipo de cambio</div>
                <div className="font-medium text-gray-900">
                  1 {getSourceCurrency()} = {result.exchangeRate} {getTargetCurrency()}
                </div>
              </div>
            </div>

            <div className="text-center pt-4">
              <Button 
                onClick={handleWhatsApp}
                className="bg-green-600 hover:bg-green-700 text-white px-8 py-3 text-lg font-medium"
                size="lg"
              >
                💬 Contactar por WhatsApp
              </Button>
              <p className="text-xs text-gray-500 mt-2">
                Te conectaremos con nuestro equipo para completar la transferencia
              </p>
            </div>
          </div>
        )}

        {/* Información adicional */}
        <div className="text-center text-sm text-gray-500 space-y-1">
          <p>• Comisión: 2% días laborables, 3% fines de semana</p>
          <p>• Tipos de cambio actualizados diariamente</p>
          <p>• Transferencias seguras y confiables</p>
        </div>
      </CardContent>
    </Card>
  );
}
